import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import './org.css'

export class registration extends Component {
    render() {
        return (
            <div className="shadow-lg p-3 width mx-auto bg-body rounded border border-2 rounded-3 mt-5">
                <form>
                <h3 className="text-center p-1 text-white bg-primary">Register</h3>

                
                <div className="form-group">
                    <label>User Name</label>
                    <input type="text" className="form-control" placeholder="Enter User Name" />
                </div>
                <div className="form-group ">
                    <label>First name</label>
                    <input type="text" className="form-control" placeholder="ENter First name" />
                </div>

                <div className="form-group">
                    <label>Last name</label>
                    <input type="text" className="form-control" placeholder="Enter Last name" />
                </div>

                <div className="form-group">
                    <label>Email</label>
                    <input type="email" className="form-control" placeholder="Enter email" />
                </div>

                <div className="form-group">
                    <label>Password</label>
                    <input type="password" className="form-control" placeholder="Enter password" />
                </div>

                <button type="submit" className="btn btn-dark mt-3 btn-lg btn-block">Register</button>
                <p className="text-end">
                    Already registered  <Link to='/login'>Login</Link>
                </p>
            </form>
            </div>
        )
    }
}

export default registration



